import Vue from "vue";
import Router from "vue-router";
import Home from "@/views/Home.vue";

Vue.use(Router);

export default new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "/",
      redirect: '/home'
    },
    {
      path: "/home",
      name: "home",
      component: Home
    },
    {
      path: "/careers",
      name: "careers",
      component: () =>
        import("@/views/Careers.vue")
    },
    {
      path: "/work",
      name: "work",
      component: () =>
        import("@/views/Work.vue")
    },
    {
      path: "/contact",
      name: "contact",
      component: () =>
        import("@/views/Contact.vue")
    },
  ]
});
